:root {
    --pill-dropdown-padding: var(--space-100);
}

@keyframes dropdownAppear {
    from {
        opacity: 0;
        transform: translateY(-15px) scale(0.9);
    }
    to {
        opacity: 1;
        transform: translateY(0) scale(1);
    }
}

.PillDropdown {
    z-index: 500;
    max-height: 440px;
    max-width: 600px;
    transform-origin: top left;
    animation: dropdownAppear var(--transition-duration-quick) forwards;
    animation-timing-function: linear;
    overflow-y: auto;
    padding: var(--pill-dropdown-padding);
    composes: OneUI-label-text from global;

    &--noPadding {
        padding: 0;
    }
}
